export default{
	name:'search',
	data(){
		return{
		
		}
	},
	methods:{
		scanBarcode(){
			uni.scanCode({
			    success: function (res) {
			        console.log('条码类型：' + res.scanType);
			        console.log('条码内容：' + res.result);
					uni.navigateTo({
						url:"../outside_of_this_app/oustside_view?o_web="+encodeURIComponent(JSON.stringify(res.result)),
						
						fail:(f)=>{
							console.log(f)
						},
					})
					
			    }
			});
		}
	}
}
